.top-section {
  background: #fff;
  height: 140px;
  padding: 0 60px;
  position: relative;
}
.top-section .logo {
  display: flex;
  position: relative;
  top: 18px;
}
.top-section .logo-wrapper {
  position: relative;
  top: 7px;
  width: 80px;
  height: 80px;
  overflow: hidden;
  margin-right: 6px;
}
.top-section .logo-wrapper img {
  position: relative;
  top: -2px;
  left: -2px;
  width: 83px;
  height: 83px;
}
.top-section .logo-txt h1 {
  font-size: 32px;
  position: relative;
  top: 4px;
  letter-spacing: 2px;
  font-weight: 500;
  margin-bottom: 0;
  color: #082541;
  font-family: '华文楷体', '宋体', '微软雅黑', 'Microsoft Yahei', Helvetica, 'Hiragino Sans GB', Arial, sans-serif;
}
.top-section .logo-txt p {
  color: #b83233;
  font-size: 18px;
  position: relative;
  top: -26px;
  left: 2px;
}
.top-section .contact-info {
  position: absolute;
  color: #082541;
  font-size: 18px;
  right: 90px;
  top: 32px;
}
