a,
abbr,
acronym,
address,
applet,
big,
blockquote,
body,
caption,
cite,
code,
dd,
del,
dfn,
div,
dl,
dt,
em,
fieldset,
font,
form,
h1,
h2,
h3,
h4,
h5,
h6,
html,
iframe,
img,
ins,
kbd,
label,
legend,
li,
object,
ol,
p,
pre,
q,
s,
samp,
small,
span,
strike,
strong,
sub,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
tr,
tt,
ul,
var {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: inherit;
  font-style: inherit;
  font-family: "PingFang SC", "Microsoft yahei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  vertical-align: baseline;
}
body,
html {
  height: 100%;
}
body {
  font-size: 14px;
}
[v-cloak] {
  display: none;
}
:focus {
  outline: 0;
}
a,
a:active,
a:hover,
a:link,
a:visited {
  color: inherit;
  text-decoration: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  word-break: keep-all;
  font-weight: normal;
}
iframe,
img {
  border: none;
  text-decoration: none;
}
ol,
ul {
  list-style: none;
}
button,
input,
select,
textarea {
  font-size: 100%;
  font-family: inherit;
}
select {
  margin: inherit;
}
hr {
  margin: 0;
  padding: 0;
  border: 0;
  color: #000;
  background-color: #000;
  height: 1px;
}
i {
  font-style: normal;
}
.clearfix:after {
  content: ".";
  display: block;
  clear: both;
  overflow: hidden;
  height: 0;
  visibility: visible;
}
@media only screen and (max-width: 800px) {
  .languageselect {
    display: block;
    position: static;
    text-align: center;
    padding: 6px 0 6px;
    margin-right: 0;
  }
}
