.left-side {
  margin-right: 24px;
}
.left-side .side-card {
  margin-bottom: 20px;
  width: 300px;
}
.left-side .side-card-title {
  line-height: 62px;
  text-align: center;
  color: #fff;
  font-size: 22px;
  background: #001529;
  font-weight: bold;
  margin: 0;
}
.left-side .side-card-content {
  padding: 15px;
  border: 1px solid #ebebeb;
  padding-bottom: 32px;
}
.left-side .side-card-content .news-list-item a {
  margin-top: 15px;
  display: block;
  font-size: 14px;
  line-height: 20px;
  color: #666;
}
.left-side .side-card-content .news-list-item a::before {
  content: '';
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #d1d1d1 url(http://www.zjwzhefeng.com/pic/icon/xypg-left-jt.png) -1px -16px no-repeat;
  vertical-align: middle;
  margin-right: 10px;
}
.left-side .side-card-content .news-list a:hover {
  color: #0b2143;
  text-decoration: underline;
}
.left-side .side-card-content .news-list a:hover::before {
  background-color: #0b2143;
}
.left-side .side-card-content .contact-container .tel {
  background: url('http://www.zjwzhefeng.com/pic/icon/xypg-right-phone.png') left 5px no-repeat;
  padding-left: 70px;
  margin-bottom: 15px;
}
.left-side .side-card-content .contact-container .tel h4 {
  font-weight: normal;
  margin: 0;
  font-size: 14px;
  color: #666;
}
.left-side .side-card-content .contact-container .tel p {
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: #ab001a;
  margin-bottom: 0;
}
.left-side .side-card-content .contact-container .company {
  font-size: 15px;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}
.left-side .side-card-content .contact-container .contact-info > p {
  margin-bottom: 0;
  border-bottom: 1px dotted #b8b8b8;
  font-size: 14px;
  color: #333;
  padding: 8px 0;
}
.left-side .side-card-content .contact-container .contact-info > p p {
  display: flex;
  flex-direction: column;
  padding-left: 42px;
  position: relative;
  margin-top: -24px;
}
.left-side .side-card-content .contact-container .contact-info > p p span {
  display: block;
  margin-top: 4px;
}
